.exhibition-card {
  background: linear-gradient(145deg, rgba(255, 237, 213, 0.9) 0%, rgba(254, 215, 170, 0.85) 30%, rgba(253, 186, 116, 0.8) 70%, rgba(251, 146, 60, 0.9) 100%);
  border-radius: 14px;
  box-shadow: 0 8px 21px rgba(251, 146, 60, 0.12), 0 3px 8px rgba(249, 115, 22, 0.08);
  overflow: hidden;
  transition: all 0.3s ease;
  cursor: pointer;
  height: 450px; /* 增加高度確保場館信息完整顯示 */
  display: flex;
  flex-direction: column;
  position: relative;
  border: 1px solid rgba(249, 115, 22, 0.2);
  backdrop-filter: blur(7px);
  flex: 0 0 calc(33.333% - 1.2rem); /* 減少間隔，增加寬度 */
  width: calc(33.333% - 1.2rem);
  max-width: calc(33.333% - 1.2rem);
}

.exhibition-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 11px 28px rgba(0, 0, 0, 0.12), 0 4px 11px rgba(79, 70, 229, 0.1);
}

.exhibition-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(79, 70, 229, 0.02) 0%, rgba(124, 58, 237, 0.02) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.exhibition-card:hover::before {
  opacity: 1;
}

.card-image {
  width: 100%;
  height: 187px; /* 減少1/3高度：280px * 2/3 ≈ 187px */
  overflow: hidden;
  flex-shrink: 0;
}

.card-content {
  padding: 1.5rem;
  flex: 1;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, rgba(255, 253, 248, 0.95) 0%, rgba(254, 249, 235, 0.9) 50%, rgba(253, 246, 220, 0.85) 100%);
  backdrop-filter: blur(6px);
  border-top: 1px solid rgba(249, 115, 22, 0.08);
  min-width: 0; /* 確保flexbox子元素可以收縮 */
  overflow-wrap: break-word;
  height: 263px; /* 增加文字區高度確保場館信息顯示 */
}

.card-header {
  margin-bottom: 0.7rem;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.card-badges {
  display: flex;
  gap: 0.8rem;
  align-items: center;
  justify-content: flex-start; /* 左對齊 */
  min-height: 2.5rem; /* 固定標籤區域高度 */
}

.card-title {
  font-size: 1.3rem;
  font-weight: 700;
  color: #333;
  margin: 0;
  line-height: 1.4;
  min-height: 4rem; /* 增加高度以容納3行文字 */
  max-height: 4rem;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3; /* 允許顯示3行 */
  -webkit-box-orient: vertical;
  letter-spacing: -0.01em;
  word-wrap: break-word;
  hyphens: auto;
}


/* 藝術 - 紫色 */
.card-category.category-藝術 {
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
  color: white;
  box-shadow: 0 3px 11px rgba(139, 92, 246, 0.3);
}

/* 科學教育 - 藍色 */
.card-category.category-科學教育 {
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: white;
  box-shadow: 0 3px 11px rgba(59, 130, 246, 0.3);
}

/* 自然 - 綠色 */
.card-category.category-自然 {
  background: linear-gradient(135deg, #10b981 0%, #047857 100%);
  color: white;
  box-shadow: 0 3px 11px rgba(16, 185, 129, 0.3);
}

/* 其他 - 橘色 */
.card-category.category-其他 {
  background: linear-gradient(135deg, #f97316 0%, #ea580c 100%);
  color: white;
  box-shadow: 0 3px 11px rgba(249, 115, 22, 0.3);
}

/* 歷史文化 - 粉紅色 */
.card-category.category-歷史文化 {
  background: linear-gradient(135deg, #ec4899 0%, #db2777 100%);
  color: white;
  box-shadow: 0 3px 11px rgba(236, 72, 153, 0.3);
}

/* 標籤基礎樣式 - 確保完全相等 */
.card-category, .card-region {
  padding: 0.56rem 1.12rem;
  border-radius: 18px;
  font-size: 0.84rem;
  font-weight: 600;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-width: 4rem; /* 確保最小寬度一致 */
  height: 2rem; /* 固定高度 */
  line-height: 1;
  white-space: nowrap;
}

/* 北部 - 青色 */
.card-region.region-北部 {
  background: linear-gradient(135deg, #06b6d4 0%, #0891b2 100%);
  color: white;
  box-shadow: 0 2px 6px rgba(6, 182, 212, 0.25);
}

/* 中部 - 黃色 */
.card-region.region-中部 {
  background: linear-gradient(135deg, #eab308 0%, #ca8a04 100%);
  color: white;
  box-shadow: 0 2px 6px rgba(234, 179, 8, 0.25);
}

/* 南部 - 紅色 */
.card-region.region-南部 {
  background: linear-gradient(135deg, #dc2626 0%, #b91c1c 100%);
  color: white;
  box-shadow: 0 2px 6px rgba(220, 38, 38, 0.25);
}

/* 東部 - 紫色 */
.card-region.region-東部 {
  background: linear-gradient(135deg, #7c3aed 0%, #5b21b6 100%);
  color: white;
  box-shadow: 0 2px 6px rgba(124, 58, 237, 0.25);
}

/* 其他區域 - 灰色 */
.card-region.region-其他 {
  background: linear-gradient(135deg, #64748b 0%, #475569 100%);
  color: white;
  box-shadow: 0 2px 6px rgba(100, 116, 139, 0.25);
}

/* 默認類別標籤樣式 */
.card-category:not([class*="category-"]) {
  background: linear-gradient(135deg, #4f46e5 0%, #7c3aed 100%);
  color: white;
  box-shadow: 0 3px 11px rgba(79, 70, 229, 0.2);
}

.card-details {
  margin-bottom: 1rem;
  flex-shrink: 0;
  min-height: 4rem; /* 減少最小高度，為其他區域留更多空間 */
}

.card-details p {
  margin: 0.42rem 0;
  font-size: 0.91rem;
  color: #666;
  line-height: 1.6;
  font-weight: 500;
  word-wrap: break-word;
  hyphens: auto;
}

/* 移除簡介容器相關CSS */

.card-footer {
  flex-shrink: 0;
  margin-top: auto;
}

.view-details-btn {
  display: block;
  background: linear-gradient(135deg, #4f46e5 0%, #7c3aed 100%);
  color: white;
  padding: 0.98rem 1.75rem;
  border: none;
  border-radius: 11px;
  font-size: 0.98rem;
  font-weight: 600;
  transition: all 0.3s ease;
  width: 100%;
  text-align: center;
  box-shadow: 0 3px 11px rgba(79, 70, 229, 0.2);
  letter-spacing: 0.01em;
  cursor: pointer;
  font-family: inherit;
}

.view-details-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 18px rgba(79, 70, 229, 0.3);
  background: linear-gradient(135deg, #3730a3 0%, #6b21a8 100%);
}


@media (max-width: 1200px) {
  .exhibition-card {
    flex: 0 0 calc(33.333% - 1rem); /* 增加寬度 */
    width: calc(33.333% - 1rem);
    max-width: calc(33.333% - 1rem);
    height: 650px; /* 平板版固定高度 */
  }
  
  .card-image {
    height: 200px; /* 平板版圖片高度減少1/3：300px * 2/3 = 200px */
  }
  
  .card-content {
    padding: 2rem;
  }
  
  .card-title {
    font-size: 1.8rem;
  }
  
  .card-details p {
    font-size: 1.2rem;
  }
  
  .card-introduction {
    font-size: 1.2rem;
  }
}

@media (max-width: 968px) {
  .exhibition-card {
    flex: 0 0 calc(50% - 0.8rem); /* 增加寬度 */
    width: calc(50% - 0.8rem);
    max-width: calc(50% - 0.8rem);
    height: 600px; /* 小平板版固定高度 */
  }
  
  .card-image {
    height: 187px; /* 小平板版圖片高度減少1/3：280px * 2/3 ≈ 187px */
  }
  
  .card-content {
    padding: 1.8rem;
  }
  
  .card-title {
    font-size: 1.6rem;
    min-height: auto;
    line-height: 1.4;
  }
  
  .card-details {
    min-height: auto;
    margin-bottom: 1.2rem;
  }
  
  .card-details p {
    font-size: 1.1rem;
    line-height: 1.5;
  }
  
  .card-introduction {
    font-size: 1.1rem;
    line-height: 1.6;
  }
  
  .view-details-btn {
    padding: 1.2rem 2rem;
    font-size: 1.3rem;
  }
}

@media (max-width: 768px) {
  .exhibition-card {
    flex: 0 0 100%; /* 手機版保持100%寬度 */
    width: 100%;
    max-width: 100%;
    height: 550px; /* 手機版固定高度 */
  }
  
  .card-image {
    height: 160px; /* 手機版圖片高度減少1/3：240px * 2/3 = 160px */
  }
  
  .card-content {
    padding: 1.5rem;
  }
  
  .card-title {
    font-size: 1.4rem;
    min-height: auto;
    line-height: 1.4;
    margin-bottom: 1rem;
  }
  
  .card-details {
    min-height: auto;
  }
  
  .card-details p {
    font-size: 1rem;
    line-height: 1.5;
    margin: 0.5rem 0;
  }
  
  .card-introduction {
    font-size: 1rem;
    line-height: 1.6;
  }
  
  .view-details-btn {
    padding: 1rem 1.5rem;
    font-size: 1.2rem;
  }
}

@media (max-width: 480px) {
  .card-image {
    height: 200px;
  }
  
  .card-content {
    padding: 1.2rem;
  }
  
  .card-title {
    font-size: 1.2rem;
    min-height: auto;
    line-height: 1.4;
    margin-bottom: 0.8rem;
  }
  
  .card-category {
    font-size: 1rem;
    padding: 0.6rem 1.2rem;
    margin-bottom: 0.6rem;
  }
  
  .card-details {
    min-height: auto;
    margin-bottom: 1rem;
  }
  
  .card-details p {
    font-size: 0.9rem;
    line-height: 1.5;
    margin: 0.4rem 0;
  }
  
  .card-introduction {
    font-size: 0.9rem;
    line-height: 1.6;
  }
  
  .view-details-btn {
    padding: 0.8rem 1.2rem;
    font-size: 1rem;
  }
}